<template>
  <div class="cart-page">
    <div class="cart-header">
      <h1>Shopping Cart</h1>
      <p v-if="cart.state.items.length" class="items-count">
        {{ cart.state.items.length }} items in your cart
      </p>
    </div>

    <div class="cart-content" v-if="cart.state.items.length">
      <div class="cart-items">
        <div v-for="item in cart.state.items" :key="item.id" class="cart-item">
          <img :src="item.image_url" :alt="item.name">
          <div class="item-info">
            <h3>{{ item.name }}</h3>
            <div class="item-meta">
              <div class="quantity-controls">
                <button @click="decrementQuantity(item)">-</button>
                <span>{{ item.quantity }}</span>
                <button @click="incrementQuantity(item)">+</button>
              </div>
              <div class="price">${{ (item.price * item.quantity).toFixed(2) }}</div>
            </div>
          </div>
          <button class="remove-button" @click="removeItem(item)">
            <span class="material-symbols-rounded">delete</span>
          </button>
        </div>
      </div>

      <div class="cart-summary">
        <h2>Order Summary</h2>
        <div class="summary-details">
          <div class="summary-row">
            <span>Subtotal</span>
            <span>${{ cart.state.total.toFixed(2) }}</span>
          </div>
          <div class="summary-row">
            <span>Shipping</span>
            <span>Free</span>
          </div>
          <div class="summary-total">
            <span>Total</span>
            <span>${{ cart.state.total.toFixed(2) }}</span>
          </div>
        </div>
        <form @submit.prevent="startCheckout" class="checkout-form">
          <div class="form-group">
            <label>Street and House Number</label>
            <input 
              v-model="checkoutInfo.address"
              type="text"
              required
              placeholder="e.g., Hoofdstraat 1"
            >
          </div>
          <div class="form-row">
            <div class="form-group">
              <label>Postal Code</label>
              <input 
                v-model="checkoutInfo.postalCode"
                type="text"
                pattern="[1-9][0-9]{3}\s?[A-Za-z]{2}"
                required
                placeholder="1234 AB"
                @input="formatPostalCode"
              >
            </div>
            <div class="form-group">
              <label>City</label>
              <input 
                v-model="checkoutInfo.city"
                type="text"
                required
                placeholder="Amsterdam"
              >
            </div>
          </div>
          <div class="form-group">
            <label>Country</label>
            <input 
              type="text" 
              value="Netherlands" 
              disabled
              class="disabled-input"
            >
          </div>
          <div class="form-group">
            <label>Phone Number</label>
            <input 
              v-model="checkoutInfo.phone"
              type="tel"
              pattern="^(\+31|0)[0-9]{9}$"
              required
              placeholder="+31612345678"
              @input="formatPhoneNumber"
            >
          </div>
          <button 
            type="submit" 
            class="checkout-button"
            :disabled="!isFormValid || cart.state.items.length === 0"
          >
            <span class="material-symbols-rounded">payments</span>
            Pay €{{ cart.state.total.toFixed(2) }}
          </button>
        </form>
      </div>
    </div>

    <div v-else class="empty-cart">
      <span class="material-symbols-rounded">shopping_cart</span>
      <h2>Your cart is empty</h2>
      <p>Looks like you haven't added anything to your cart yet</p>
      <router-link to="/store" class="continue-shopping">
        Continue Shopping
      </router-link>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, computed } from 'vue'
import cart from '@/store/cart'
import { loadStripe } from '@stripe/stripe-js'
import { supabase } from '@/services/supabase'
import auth from '@/store/auth'

export default defineComponent({
  name: 'Cart',
  setup() {
    const checkoutInfo = ref({
      address: '',
      city: '',
      postalCode: '',
      phone: '',
      country: 'NL' // Fixed to Netherlands
    })

    const isFormValid = computed(() => {
      // Log values for debugging
      console.log('Form values:', {
        address: checkoutInfo.value.address,
        city: checkoutInfo.value.city,
        postalCode: checkoutInfo.value.postalCode,
        phone: checkoutInfo.value.phone,
        regex: {
          postal: /^[1-9][0-9]{3}\s?[A-Za-z]{2}$/.test(checkoutInfo.value.postalCode),
          phone: /^(\+31|0)[0-9]{9}$/.test(checkoutInfo.value.phone)
        }
      });

      return (
        checkoutInfo.value.address.length >= 3 && // Reduced from 5 to 3
        checkoutInfo.value.city.length >= 2 &&
        /^[1-9][0-9]{3}\s?[A-Za-z]{2}$/.test(checkoutInfo.value.postalCode) &&
        /^(\+31|0)[0-9]{9}$/.test(checkoutInfo.value.phone) &&
        cart.state.items.length > 0
      )
    })

    function formatPostalCode(event) {
      let value = event.target.value.toUpperCase()
      value = value.replace(/[^0-9A-Z]/g, '')
      
      if (value.length > 4) {
        value = value.slice(0, 4) + ' ' + value.slice(4, 6)
      }
      
      checkoutInfo.value.postalCode = value
    }

    function formatPhoneNumber(event) {
      let value = event.target.value.replace(/[^\d+]/g, '')
      
      if (!value.startsWith('+31')) {
        if (value.startsWith('0')) {
          value = '+31' + value.slice(1)  // Fix: Replace leading 0 with +31
        } else {
          value = '+31' + value
        }
      }
      
      checkoutInfo.value.phone = value
    }

    async function startCheckout() {
      try {
        // First try Stripe checkout
        const stripe = await loadStripe(process.env.VUE_APP_STRIPE_PUBLIC_KEY)
          .catch(() => null); // Catch Stripe loading failure

        if (stripe) {
          // Try Stripe checkout first
          try {
            const { data, error } = await supabase.functions.invoke('create-payment', {
              body: {
                items: cart.state.items.map(item => ({
                  ...item,
                  price_in_cents: Math.round(item.price * 100)
                })),
                email: auth.state.user.email,
                user_id: auth.state.user.id,
                shipping_address: checkoutInfo.value
              }
            });

            if (!error && data.sessionId) {
              const { error: stripeError } = await stripe.redirectToCheckout({
                sessionId: data.sessionId
              });
              
              if (!stripeError) return; // Successful redirect to Stripe
            }
          } catch (e) {
            console.warn('Stripe checkout failed, falling back to manual order:', e);
          }
        }

        // Fallback to manual order processing
        const { data: order, error: orderError } = await supabase
          .from('orders')
          .insert({
            user_id: auth.state.user.id,
            items: cart.state.items,
            total_amount: cart.state.total,
            shipping_address: checkoutInfo.value,
            status: 'pending',
            payment_method: 'manual',
            created_at: new Date().toISOString()
          })
          .select()
          .single();

        if (orderError) throw orderError;

        // Clear cart and redirect to success page
        cart.clear();
        alert('Your order has been placed and is pending admin approval.');
        router.push('/checkout/success?order_id=' + order.id);

      } catch (error) {
        console.error('Checkout error:', error);
        alert('Payment failed. Please try again.');
      }
    }

    function incrementQuantity(item) {
      cart.updateQuantity(item.id, item.quantity + 1)
    }

    function decrementQuantity(item) {
      if (item.quantity > 1) {
        cart.updateQuantity(item.id, item.quantity - 1)
      }
    }

    function removeItem(item) {
      cart.removeItem(item.id)
    }

    return {
      cart,
      checkoutInfo,
      startCheckout,
      incrementQuantity,
      decrementQuantity,
      removeItem,
      isFormValid,
      formatPostalCode,
      formatPhoneNumber
    }
  }
})
</script>

<style scoped>
.cart-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 24px;
}

.cart-header {
  margin-bottom: 40px;
}

.cart-header h1 {
  font-size: 32px;
  margin: 0;
  background: linear-gradient(135deg, var(--text-primary), var(--text-secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.items-count {
  color: var(--text-secondary);
  margin: 8px 0 0;
}

.cart-content {
  display: grid;
  grid-template-columns: 1fr 320px;
  gap: 32px;
  align-items: start;
}

.cart-items {
  background: var(--card-background);
  border-radius: 24px;
  border: 1px solid var(--card-border);
  overflow: hidden;
}

.cart-item {
  position: relative;
  display: grid;
  grid-template-columns: 100px 1fr auto;
  align-items: center;
  gap: 16px;
  padding: 16px;
}

.cart-item:last-child {
  border-bottom: none;
}

.cart-item img {
  width: 100px;
  height: 100px;
  object-fit: contain;
  border-radius: 12px;
}

.item-info {
  flex: 1;
}

.item-info h3 {
  margin: 0 0 12px;
  font-size: 18px;
}

.item-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.quantity-controls {
  display: inline-flex;
  align-items: center;
  background: var(--surface-background);
  border-radius: 8px;
  overflow: hidden;
}

.quantity-controls button {
  width: 32px;
  height: 32px;
  border: none;
  background: none;
  color: var(--text-primary);
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.quantity-controls button:hover {
  background: var(--background-elevated);
}

.quantity-controls span {
  min-width: 32px;
  text-align: center;
}

.price {
  font-size: 18px;
  font-weight: 600;
  color: var(--accent-primary);
}

.remove-button {
  background: none;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  padding: 8px;
  border-radius: 8px;
}

.remove-button:hover {
  background: var(--surface-background);
  color: var(--system-red);
}

.cart-summary {
  background: var(--card-background);
  border-radius: 24px;
  border: 1px solid var(--card-border);
  padding: 24px;
  position: sticky;
  top: 100px;
}

.cart-summary h2 {
  margin: 0 0 24px;
  font-size: 24px;
}

.summary-details {
  margin-bottom: 24px;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
  color: var(--text-secondary);
}

.summary-total {
  display: flex;
  justify-content: space-between;
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid var(--card-border);
  font-weight: 600;
  font-size: 18px;
}

.checkout-button {
  width: 100%;
  padding: 16px;
  border: none;
  border-radius: 12px;
  background: var(--accent-primary);
  color: white;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.2s ease;
}

.checkout-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.checkout-button:disabled {
  background: var(--surface-background);
  color: var(--text-secondary);
  cursor: not-allowed;
  transform: none;
}

.empty-cart {
  text-align: center;
  padding: 80px 24px;
}

.empty-cart .material-symbols-rounded {
  font-size: 64px;
  color: var(--text-secondary);
  margin-bottom: 24px;
}

.empty-cart h2 {
  margin: 0 0 8px;
  font-size: 24px;
}

.empty-cart p {
  color: var(--text-secondary);
  margin: 0 0 32px;
}

.continue-shopping {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  background: var(--accent-primary);
  color: white;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 600;
  transition: all 0.2s ease;
}

.continue-shopping:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.checkout-form {
  margin-top: 24px;
  border-top: 1px solid var(--card-border);
  padding-top: 24px;
}

.form-group {
  margin-bottom: 16px;
}

.form-group label {
  display: block;
  font-size: 14px;
  color: var(--text-secondary);
  margin-bottom: 8px;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 12px;
  border: 1px solid var(--card-border);
  border-radius: 8px;
  background: var(--surface-background);
  color: var(--text-primary);
  font-size: 14px;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 2px var(--accent-primary-alpha);
}

input:invalid {
  border-color: var(--system-red);
}

.disabled-input {
  background: var(--background-elevated) !important;
  cursor: not-allowed;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

@media (max-width: 768px) {
  .form-row {
    grid-template-columns: 1fr;
  }
  
  .cart-content {
    grid-template-columns: 1fr;
  }
  
  .cart-summary {
    position: static;
  }
  
  .cart-item {
    grid-template-columns: 80px 1fr;
  }
  
  .cart-item img {
    width: 80px;
    height: 80px;
  }
  
  .remove-button {
    position: absolute;
    top: 8px;
    right: 8px;
  }
}
</style>
